  top: 0;
}

.main-nav {
  height: 8vh;
  min-height: 60px;
  width: 100%;
  background-color: #fffffe;
  border-bottom: 2px solid #efefef;
  justify-content: space-between;
  align-items: center;
  padding: 2%;
  display: flex;
  position: relative;
  top: 0;
}

.main-nav h1 {
  margin-left: 10px;
  font-family: Dancing Script, cursive;
  font-size: 2.5rem;
}

.main-nav span {
  font-size: 2rem;
  display: block;
}

.post {
  width: 100%;
  margin-bottom: 30px;
  animation: post-show .2s;
}

.post .post-top-bar {
  justify-content: space-between;
  padding: 2%;
