  border-radius: 50px;
}

.post .post-top-bar .button-post-option span {
  font-size: 2rem;
}

.post .post-photo img {
  width: 100%;
  aspect-ratio: 1 / 1;
}

.post .post-bottom-segment {
  padding: 0 2%;
}

.post .post-desc details {
  margin: 10px 0;
}

.post .post-desc details .details-username {
  font-weight: bold;
}

.post .post-desc details .show-hide-btn-post {
  cursor: pointer;
  color: rgba(2, 2, 2, .6);
  font-weight: bold;
  display: inline;
}

.post .post-desc summary {
  align-items: baseline;
  gap: .3rem;
