  gap: 10px;
  display: flex;
}

.post .post-buttons span {
  font-size: 2rem;
}

@keyframes post-show {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

* {
  box-sizing: border-box;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin: 0;
  padding: 0;
}

body {
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
